.login {
    width: 360px;
    height: 450px;
    background-color: #fff;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 80px auto;
  }
  @media (max-width: 450px) {
    .login {
      margin-left: 50px;
    }
  }
  ::placeholder {
    font-family: 'Lato', sans-serif;
    text-align: center;
  }
  
  .login__input {
    width: 300px;
    height: 45px;
    border: 1px solid #ccc;
    margin-left: 30px;
    margin-top: 13px;
  }
  
  .login__input {
    outline-color: #1976d2;
  }
  
  button {
    width: 250px;
    height: 45px;
    font-size: 1rem;
    margin-top: 22px;
    margin-left: 50px;
    border-radius: 4px;
    border: none;
    color: white;
  }
  
  .login__button {
    background-color: #1976d2;
  }
  
  .register-button {
    background-color: #fff;
    border: 1px solid #1976d2;
    color: #1976d2;
  }
  
  .register-button:hover {
    background-color: #1976d2;
    color: #fff;
    transition: 0.5s;
  }
  