.navbar {
    width: 100%;
    height: 60px;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.05);
    display: flex;
    justify-content: space-between;
  }
  
  span {
    color: #1976d2;
  }
  
  .navbar__leftside,
  .navbar__rightside {
    padding: 5px;
  }
  .navbar__brand {
    margin-top: 0;
    padding-top: 6px;
    padding-bottom: 3px;
    font-size: 1.5rem;
    padding-left: 7px;
    font-weight: bold;
    color: #000;
  }
  
  .navbar__login,
  .navbar__register {
    width: 130px;
    margin-top: -5px;
    margin-left: 6px;
  }
  
  .navbar__register {
    border: 1px solid #1976d2;
    background-color: #fff;
    color: #1976d2;
  }
  
  .navbar__register:hover {
    background-color: #1976d2;
    color: #fff;
    transition: 0.5s;
  }
  
  .Link {
    text-decoration: none;
    color: #000;
  }
  .navbar__menu {
    width: 100px;
    height: 40px;
  }
  
  @media (max-width: 450px) {
    .navbar__brand {
      font-size: 22px;
    }
  }
  
  @media (max-width: 280px) {
    .navbar__brand {
     font-size: 22px;
    }
    .navbar__login,
    .navbar__register {
      width: 90px;
      margin-top: 0;
      margin-left: 6px;
    }
  }
  
  @media (max-width:450px) {
    .navbar {
      width: 450px;
    }
  }
  