@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@1,300&display=swap);
.login {
    width: 360px;
    height: 450px;
    background-color: #fff;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 80px auto;
  }
  @media (max-width: 450px) {
    .login {
      margin-left: 50px;
    }
  }
  ::-webkit-input-placeholder {
    font-family: 'Lato', sans-serif;
    text-align: center;
  }
  :-ms-input-placeholder {
    font-family: 'Lato', sans-serif;
    text-align: center;
  }
  ::-ms-input-placeholder {
    font-family: 'Lato', sans-serif;
    text-align: center;
  }
  ::placeholder {
    font-family: 'Lato', sans-serif;
    text-align: center;
  }
  
  .login__input {
    width: 300px;
    height: 45px;
    border: 1px solid #ccc;
    margin-left: 30px;
    margin-top: 13px;
  }
  
  .login__input {
    outline-color: #1976d2;
  }
  
  button {
    width: 250px;
    height: 45px;
    font-size: 1rem;
    margin-top: 22px;
    margin-left: 50px;
    border-radius: 4px;
    border: none;
    color: white;
  }
  
  .login__button {
    background-color: #1976d2;
  }
  
  .register-button {
    background-color: #fff;
    border: 1px solid #1976d2;
    color: #1976d2;
  }
  
  .register-button:hover {
    background-color: #1976d2;
    color: #fff;
    transition: 0.5s;
  }
  
.navbar {
    width: 100%;
    height: 60px;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.05);
    display: flex;
    justify-content: space-between;
  }
  
  span {
    color: #1976d2;
  }
  
  .navbar__leftside,
  .navbar__rightside {
    padding: 5px;
  }
  .navbar__brand {
    margin-top: 0;
    padding-top: 6px;
    padding-bottom: 3px;
    font-size: 1.5rem;
    padding-left: 7px;
    font-weight: bold;
    color: #000;
  }
  
  .navbar__login,
  .navbar__register {
    width: 130px;
    margin-top: -5px;
    margin-left: 6px;
  }
  
  .navbar__register {
    border: 1px solid #1976d2;
    background-color: #fff;
    color: #1976d2;
  }
  
  .navbar__register:hover {
    background-color: #1976d2;
    color: #fff;
    transition: 0.5s;
  }
  
  .Link {
    text-decoration: none;
    color: #000;
  }
  .navbar__menu {
    width: 100px;
    height: 40px;
  }
  
  @media (max-width: 450px) {
    .navbar__brand {
      font-size: 22px;
    }
  }
  
  @media (max-width: 280px) {
    .navbar__brand {
     font-size: 22px;
    }
    .navbar__login,
    .navbar__register {
      width: 90px;
      margin-top: 0;
      margin-left: 6px;
    }
  }
  
  @media (max-width:450px) {
    .navbar {
      width: 450px;
    }
  }
  
body {
    background-color: #f3f3f3;
}
.actions {
    display: flex;
}

.btns {
    width: 50px;
}
.box-table {
    width: 530px;
    background-color: #fff;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.15);
    display: flex;
    margin: 50px auto;
  }

  table {
    font-family: 'Lato', sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }
.request {
    width: 400px;
    height: 620px;
    background-color: #fff;
    margin: 20px auto;
    border: 1px solid #e4e5e7;
    box-shadow: 0 0 1em #eee;
  }

  .label__form{
    position: relative;
    top: 10px;
    left: 28px;
    font-size: 13px;
  }
  
  
  .input,
  .textarea {
    display: block;
    margin: 20px auto;
    text-align: center;
    outline-color:#2094ad;
  }
  .select {
    display: block;
    margin: 15px auto;
  }
  .input,
  .select {
    width: 300px;
    height: 40px;
  }
  
  .textarea {
    width: 300px;
    height: 200px;
  }
  
  .service__button {
    width: 200px;
    height: 40px;
    margin-left: 105px;
    background-color: deepskyblue;
  }
  
  .service__label {
    margin: 5px auto;
  }
  
  @media (max-width: 450px) {
    .request {
      margin-left: 15px;
    }
  }
  
