.request {
    width: 400px;
    height: 620px;
    background-color: #fff;
    margin: 20px auto;
    border: 1px solid #e4e5e7;
    box-shadow: 0 0 1em #eee;
  }

  .label__form{
    position: relative;
    top: 10px;
    left: 28px;
    font-size: 13px;
  }
  
  
  .input,
  .textarea {
    display: block;
    margin: 20px auto;
    text-align: center;
    outline-color:#2094ad;
  }
  .select {
    display: block;
    margin: 15px auto;
  }
  .input,
  .select {
    width: 300px;
    height: 40px;
  }
  
  .textarea {
    width: 300px;
    height: 200px;
  }
  
  .service__button {
    width: 200px;
    height: 40px;
    margin-left: 105px;
    background-color: deepskyblue;
  }
  
  .service__label {
    margin: 5px auto;
  }
  
  @media (max-width: 450px) {
    .request {
      margin-left: 15px;
    }
  }
  